.accordion {
  &__title {
    margin: 0 0 25px 0;

    line-height: 16px;
    font-family: $font-family--roboto;
    text-transform: uppercase;
    letter-spacing: 0.05em;

    @include viewport--sm {
      display: flex;
      justify-content: space-between;
      margin: 0;
      padding: 30px 0;
      cursor: pointer;
    }
  }

  &__content {
    @include viewport--sm {
      height: 0;
      visibility: hidden;
      overflow: hidden;
    }

    &.active {
      height: 100%;
      visibility: visible;
      padding: 0 0 20px 0;
    }
  }

  &__btn {
    display: none;

    @include viewport--sm {
      display: flex;

      cursor: pointer;

      &.active {
        .open {
          opacity: 0;
        }
      }
    }

    // svg {
    //   pointer-events: none;
    // }
  }
}
